import { supabase } from "@/lib/supabase";
import Navbar from "@/components/landing/Navbar";
import Hero from "@/components/landing/Hero";
import Marketplace from "@/components/landing/Marketplace";

export const revalidate = 0; // Ensure fresh data on every load

export default async function LandingPage() {
  
  // 1. Fetch All Active Products (Limit 100 for now)
  const { data: products } = await supabase
    .from("products")
    .select("*")
    .eq("is_active", true)
    .limit(100)
    .order("created_at", { ascending: false });

  // 2. Fetch All Stores
  const { data: stores } = await supabase
    .from("stores")
    .select("*")
    .limit(50);

  return (
    <div className="min-h-screen bg-white font-sans selection:bg-emerald-100">
      <Navbar cartCount={0} onOpenCart={() => {}} /> {/* Cart state managed inside Marketplace for now */}
      
      <Hero />
      
      <Marketplace 
        products={products || []} 
        stores={stores || []} 
      />

      {/* Footer */}
      <footer className="py-12 border-t border-gray-100 text-center bg-white">
         <p className="text-gray-400 text-sm">Â© 2025 StoreLink Inc. The African Marketplace.</p>
      </footer>
    </div>
  );
}